html {

  
  /* peri */
  --brand-peri: var(--brand-main);
  --text1-peri: var(--brand-900);
  --text2-peri: var(--brand-main);
  --text3-peri: var(--brand-600);
  --text4-peri: var(--brand-100);
  --text5-peri: var(--brand-100);
  --surface1-peri: var(--brand-100);
  --surface2-peri: var(--brand-200);
  --surface3-peri: var(--brand-300);
  --surface4-peri: var(--brand-500);
  --surface-shadow-peri: 239 30% 20%;
  --shadow-strength-peri: 2%;
  --logo-opacity: 0.3;
  
  /* dark */
  --brand-dark: var(--brand-300);
  --text1-dark: var(--gray-1);
  --text2-dark: var(--gray-3);
  --text3-dark: var(--gray-2);
  --text4-dark: var(--gray-9);
  --text5-dark: var(--gray-0);
  --surface1-dark: var(--gray-9);
  --surface2-dark: var(--brand-900);
  --surface3-dark: var(--gray-7);
  --surface4-dark: var(--gray-5);
  --surface5-dark: var(--brand-600);
  --surface-shadow-dark: 200 50% 3%;
  --shadow-strength-dark: 80%;
  --logo-opacity: 0.1;
}

:root {
--brand-main: hsl(239, 29%, 54%);
--brand-100: hsl(239, 26%, 93%);
--brand-200: hsl(239, 60%, 92%);
--brand-300: hsl(239, 39%, 83%);
--brand-400: hsl(239, 33%, 73%);
--brand-500: hsl(239, 31%, 63%);
--brand-600: hsl(238, 34%, 38%);
--brand-900: hsl(239, 40%, 20%);
  color-scheme: light;

  /* set defaults */
  --brand: var(--brand-peri);
  --text1: var(--text1-peri);
  --text2: var(--text2-peri);
  --text3: var(--text3-peri);
  --text4: var(--text4-peri);
  --text5: var(--text5-peri);
  --surface1: var(--surface1-peri);
  --surface2: var(--surface2-peri);
  --surface3: var(--surface3-peri);
  --surface4: var(--surface4-peri);
  --surface5: var(--brand-peri);
  --surface-shadow: var(--surface-shadow-peri);
  --shadow-strength: var(--shadow-strength-peri);
  --logo-opacity: 0.3;
//marquee variables
--marquee-width: 100vw;
--marquee-height: 10vh;
--marquee-elements-displayed: 12;
--marquee-element-width: calc(var(--marquee-width) / var(--marquee-elements-displayed));
--marquee-animation-duration: calc(var(--marquee-elements) * 3s);

@media screen and (max-width: 2000px) {
  --marquee-elements-displayed: 8;
}

@media screen and (max-width: 950px) {
  --marquee-elements-displayed: 6;
}

@media screen and (max-width: 600px) {
  --marquee-elements-displayed: 4
}
}

